import { ErrorAppCodes } from "../../types/error/error";

export const cartError = (action: ErrorAppCodes) => {
    switch (action) {
        case 400:
            return `   ,
            `
        case 401:
            return ` , 
                  `
        case 403:
            return ` , 
              `
        case 404:
            return `  , 
                 `
        default:
            return ''
    }
} 